.window-figure{
  max-width: 100%;
  max-height: 100%;
  min-height: 400px;
  min-width: 400px;
}
.figure-frame{
  -webkit-box-shadow: -1px 1px 5px 1px rgba(0,0,0,0.5);
          box-shadow: -1px 1px 5px 1px rgba(0,0,0,0.5);
  display: inline-block;
  padding: 10px 15px;
  max-width: 90%;
  position: relative;
}
.figure-frame:hover{
  background-color: rgba(32,35,42,0.1);
}
.figure-container{
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.figure-frame:hover>.figure-open{
  display: block;
}
.figure-open{
  display: none;
  position: absolute;
  top: -10px;
  right: -5px;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  overflow: hidden;
}
.figure-open-clip{
  height: 42px;
  margin: auto;
}
.figure{
  width: 100%;
}
.figure-loading{
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  height: 100px;
}
.figure-caption{
  text-align: left;
  font-size: 0.8rem;
  color: #555;
  font-family: sans-serif;
  margin: 0.5rem 1rem;
  line-height: 1.3;
}
.figure-n{
  font-style: italic;
  color: rgb(43,87,154);
  margin-right: 0.2rem;
}
.view-figure{
  top: 0;
  text-align: center;
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(32, 35, 42, 0.85);
  z-index: 1000000;
}
.vf-cap{
  color: #fff;
  font-size: 0.7rem;
  text-align: left;
  line-height: 1.5;
  overflow: auto;
  position: relative;
}
.vf-img-h{
  display: inline-block;
  width: 69%;
  height: 100%;
  vertical-align: top;
  padding: 30px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.vf-img-v{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align:Center;
      align-items:Center;
  padding: 30px 30px 10px;
  height: 80%;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.vf-img-v>.vf-img-img{
  max-width: 100%;
  max-height: 100%;
}
.vf-img-h>.vf-img-img{
  max-width: 100%;
  max-height: 100%;
}
.vf-cap-h{
  display: inline-block;
  width: 30%;
  max-height: 100%;
  vertical-align: bottom;
  padding: 30px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.vf-cap-v{
  max-height: 18%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 120%;
  padding: 10px 25% 0 5%;
  margin-bottom: 30px;
}
.vf-cap-v~.vf-cap-after{
  display: block;
  width: 90%;
  border: 2px solid rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  position: absolute;
  left: 5%;
  -webkit-box-shadow: 0 0 1px 1px rgb(255, 255, 255, 0.6);
          box-shadow: 0 0 1px 1px rgb(255, 255, 255, 0.6);
  -webkit-transform: translateY(-28px) scaleY(0.2);
      -ms-transform: translateY(-28px) scaleY(0.2);
          transform: translateY(-28px) scaleY(0.2);
}
.vf-cap-after{
  display: none;
}
.vf-close{
  color: #fff;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
  font-size: 1.2rem;
  font-family: 'Forum', cursive;
}



